const BASE_URL = process.env.NEXT_PUBLIC_API_URL || ''

function getApiUrl(path: string) {
  if (BASE_URL) {
    return `${BASE_URL}${path}`
  }
  // When BASE_URL is empty, use relative path for client-side fetch
  return path
}

export async function fetchHorses() {
  const response = await fetch(getApiUrl('/api/horses'))
  if (!response.ok) throw new Error('Failed to fetch horses')
  return response.json()
}

export async function createHorse(data: any) {
  const response = await fetch(getApiUrl('/api/horses'), {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
    },
    body: JSON.stringify(data),
  })
  if (!response.ok) throw new Error('Failed to create horse')
  return response.json()
}

