"use client"

import { useEffect, useState } from "react"
import { Header } from "@/components/header"
import { HorseCard } from "@/components/horse-card"
import { Button } from "@/components/ui/button"
import { fetchHorses, type Horse } from "@/lib/api"
import Link from "next/link"

export default function HorsesPage() {
  const [horses, setHorses] = useState<Horse[]>([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)

  useEffect(() => {
    async function loadHorses() {
      try {
        const data = await fetchHorses()
        setHorses(data)
      } catch (err) {
        setError("Failed to load horses")
      } finally {
        setLoading(false)
      }
    }
    loadHorses()
  }, [])

  if (loading) {
    return (
      <div className="flex min-h-screen flex-col">
        <Header />
        <main className="flex-1 flex items-center justify-center p-8">
          <p className="text-lg text-gray-500">Loading horses...</p>
        </main>
      </div>
    )
  }

  if (error) {
    return (
      <div className="flex min-h-screen flex-col">
        <Header />
        <main className="flex-1 flex items-center justify-center p-8">
          <p className="text-lg text-red-500">{error}</p>
        </main>
      </div>
    )
  }

  return (
    <div className="flex min-h-screen flex-col">
      <Header />
      <main className="flex-1 space-y-4 p-8 pt-6">
        <div className="flex items-center justify-between">
          <h2 className="text-3xl font-bold tracking-tight">Horses</h2>
          <Link href="/horses/new">
            <Button className="bg-black text-white hover:bg-gray-800">
              Add New Horse
            </Button>
          </Link>
        </div>

        {horses.length > 0 ? (
          <div className="grid gap-6 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4">
            {horses.map((horse: Horse) => (
              <HorseCard
                key={horse.id}
                id={horse.id}
                name={horse.name}
                breed={horse.breed}
                age={horse.age}
                status={horse.status}
                imageUrl={horse.imageUrl}
              />
            ))}
          </div>
        ) : (
          <div className="flex flex-col items-center justify-center space-y-4 py-12">
            <p className="text-lg text-gray-500">No horses found</p>
            <Link href="/horses/new">
              <Button variant="outline">Add your first horse</Button>
            </Link>
          </div>
        )}
      </main>
    </div>
  )
}
